html {
  font-family: 'Ubuntu Mono', monospace;
  font-size: 16px;
  line-height: 16px;
  color: #1e262c;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  max-width: calc(100% - 36px);
  width: 500px;
  margin: 36px auto;
}

.container > * {
  margin-bottom: 36px;
  display: block;
}

a {
  text-decoration: none;
  color: #1e262c;
}

a:hover {
  text-decoration: underline;
}

.tab {
  display: inline-block;
  cursor: pointer;
  border-radius: 3px;
  padding: 1px;
}

.tab:hover {
  color: white;
  background-color: #1e262c;
}

.tab-selected {
  color: white;
  background-color: #1e262c;
}

.links {
  float: right;
}

.photo {
  width: 100%;
  border-radius: 3px;
}

.border {
  margin-left: auto;
  margin-right: auto;
  border-top: 1px solid #1e262c;
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 10px;
    line-height: 10px;
  }

  .container {
    margin: 18px auto;
  }

  .container > * {
    margin-bottom: 18px;
  }
}



